.btn,
a.btn {
  text-shadow: none;
  transition: all 0.3s ease;
  padding: 12px 24px;
  border-radius: 8px;
  border: none;
  min-height: 48px;
  text-decoration: none;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  cursor: pointer;
  white-space: nowrap;
}
@media (max-width: 827px) {
  .btn,
  a.btn {
    white-space: normal;
  }
}
@media (max-width: 540px) {
  .btn,
  a.btn {
    padding: 6px 12px;
    font-size: 14px;
    line-height: 21px;
    min-height: 37px;
  }
}
.btn:hover,
a.btn:hover,
.btn:focus,
a.btn:focus {
  border: none;
  outline: none;
}
.btn:active,
a.btn:active,
.btn.active,
a.btn.active,
.btn:active:focus,
a.btn:active:focus,
.btn.active.focus,
a.btn.active.focus {
  outline: none;
}
.btn-with-icon,
a.btn-with-icon {
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.btn .icon,
a.btn .icon {
  fill: #2B313A;
}
.btn .icon-right,
a.btn .icon-right {
  margin-left: 8px;
}
.btn .icon-left,
a.btn .icon-left {
  margin-right: 8px;
}
button:focus {
  outline: none;
}
.btn-default {
  color: #2B313A;
  background-color: #fff;
}
.btn-default:hover {
  background: #D3D3D5;
}
.btn-default:active,
.btn-default.active,
.btn-default:active:focus,
.btn-default.active.focus {
  background: #B5B7B9;
}
.btn-primary,
a.btn-primary {
  color: #2B313A;
  background-color: #FFA113;
}
.btn-primary:hover,
a.btn-primary:hover {
  background: #FFB546;
  color: #2B313A;
}
.btn-primary:active,
a.btn-primary:active,
.btn-primary.active,
a.btn-primary.active,
.btn-primary:active:focus,
a.btn-primary:active:focus,
.btn-primary.active.focus,
a.btn-primary.active.focus {
  color: #2B313A;
  background: #F48400;
}
.btn-link {
  background: none;
  color: #FFB546;
  text-decoration: none;
  padding: 0;
  border: none;
  cursor: pointer;
}
.btn-link:hover,
.btn-link:focus,
.btn-link:active,
.btn-link:active:focus {
  color: #FFB546;
  border-bottom: 1px solid #FFB546;
  background: none;
}
.btn-link:focus {
  outline: none;
}
.btn[disabled],
.btn[disabled]:active,
.btn[disabled]:focus,
.btn[disabled]:hover {
  background: #E6E6E6;
  opacity: 1;
  color: #BFBFBF;
  box-shadow: none;
}
.btn-link[disabled],
.btn-link[disabled]:active,
.btn-link[disabled]:focus,
.btn-link[disabled]:hover {
  background: none;
  opacity: 0.5;
  color: #FFB546;
  text-decoration: none;
  cursor: default;
}
.btn + .btn {
  margin-left: 8px;
}
