/********** Custom-Select-Arrow ************/
select.form-control {
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M18 9L12 15L6 9' stroke='%23797C81' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
  background-position: center right 12px;
  background-repeat: no-repeat;
  appearance: none;
  padding-right: 40px;
  max-width: 100%;
  min-width: 150px;
  width: auto;
}
select.form-control::-ms-expand {
  display: none;
  /* remove arrow IE */
}
.react-numeric-input {
  position: relative;
  display: inline-block;
  max-width: 116px;
}
.react-numeric-input .form-control {
  text-align: center;
  min-width: 116px;
}
.react-numeric-input > b {
  position: absolute;
  width: 24px;
  height: 24px;
  top: 12px;
  cursor: pointer;
}
.react-numeric-input > b:first-of-type {
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12 6L12 18' stroke='%23767B87' stroke-width='2' stroke-linecap='round'/%3E%3Cpath d='M18 12L6 12' stroke='%23767B87' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E%0A");
  right: 12px;
}
.react-numeric-input > b:last-of-type {
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M18 12L6 12' stroke='%23767B87' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E%0A");
  left: 12px;
}
/********** Checkbox / Radio ************/
.visually-hidden,
.checkbox-input,
.radio-input {
  position: absolute;
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}
.radio label,
.checkbox label {
  padding-left: 22px;
}
.radio-label,
.checkbox-label {
  padding-left: 22px;
  font-weight: normal;
  position: relative;
}
.checkbox-input-icon {
  width: 16px;
  height: 16px;
  border: 2px solid #D3D3D5;
  border-radius: 1px;
  background: #fff;
  position: absolute;
  margin-top: 3px;
  margin-left: -22px;
}
.checkbox-input:checked + .checkbox-input-icon {
  background-image: url("data:image/svg+xml,%3Csvg width='10' height='8' viewBox='0 0 10 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.5 5L3.5 7L8.5 1' stroke='%23272727' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
  background-color: #FFA113;
  background-repeat: no-repeat;
  background-position: center;
  border-color: #FFA113;
}
.checkbox-input:focus + .checkbox-input-icon {
  border-color: #97999D;
}
.checkbox-input:checked:focus + .checkbox-input-icon {
  background-color: #F48400;
  border-color: #F48400;
}
.checkbox-input:disabled + .checkbox-input-icon {
  background-color: #eee;
  border-color: #eee;
}
.checkbox-input:disabled:checked + .checkbox-input-icon {
  background-image: url("data:image/svg+xml,%3Csvg width='10' height='8' viewBox='0 0 10 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.5 5L3.5 7L8.5 1' stroke='%23808080' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
}
.radio-input-icon {
  width: 16px;
  height: 16px;
  border: 2px solid #D3D3D5;
  border-radius: 50%;
  background: #fff;
  position: absolute;
  margin-top: 3px;
  margin-left: -22px;
}
.radio-input:checked + .radio-input-icon {
  background-image: url("data:image/svg+xml,%3Csvg width='8' height='8' viewBox='0 0 8 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='4' cy='4' r='4' fill='%23272727'/%3E%3C/svg%3E");
  background-color: #FFA113;
  background-repeat: no-repeat;
  background-position: center;
  border-color: #FFA113;
}
.radio-input:focus + .radio-input-icon {
  border-color: #97999D;
}
.radio-input:checked:focus + .checkbox-input-icon {
  background-color: #F48400;
  border-color: #F48400;
}
.radio-input:disabled + .radio-input-icon {
  background-color: #eee;
  border-color: #eee;
}
.radio-input:disabled:checked + .radio-input-icon {
  background-image: url("data:image/svg+xml,%3Csvg width='8' height='8' viewBox='0 0 8 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='4' cy='4' r='4' fill='%23808080'/%3E%3C/svg%3E");
}
